@import url(https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i|Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i|Playfair+Display:400,400i,700,700i,900,900i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Google fonts & font family
==================================================*/

/*
font-family: 'Playfair Display', serif;
font-family: 'Lato', sans-serif;
font-family: 'Open Sans', sans-serif;
*/

/* typography
==================================================*/

h1 {
  font-family: 'Playfair Display', serif;
  font-size: 60px;
  line-height: 50px;
}

h4 {
  font-family: 'Lato', sans-serif;
  text-transform: capitalize;
  letter-spacing: 2px;
  position: relative;
  color: #425bb5;
  margin-left: 40px;
  font-weight: 900;
}

h4:before {
  position: absolute;
  width: 30px;
  left: -40px;
  content: '';
  display: block;
  height: 2px;
  background: #425bb5;
  top: 9px;
}

/* General
==================================================*/

bodyResume {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 24px;
  width: 80%;
  margin: auto;
}

.size50 {
  font-size: 50px;
  line-height: 50px;
}

.h50 {
  height: 50px;
}
.tlinks {
  text-indent: -9999px;
  height: 0;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

@-webkit-keyframes wink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes wink {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* home section
==================================================*/

#home {
  background: url(/static/images/home-bg-2.jpg);
  background-size: cover;
}

.table-cell {
  display: table-cell;
  vertical-align: middle;
}

.disply-table {
  display: table;
  height: 100vh;
}

/* about section
==================================================*/

#about {
  padding: 100px 0;
}
.container {
  display: flex;
}
.about-img-div {
  margin: 0 auto;
}
img {
  margin: 200px 50px;
}
.about-border {
  position: relative;
  border: 10px solid #425bb5;
  width: 400px;
  height: 400px;
  top: 230px;
  left: 50px;
  z-index: 1;
  float: left;
}
.summary {
  width: 100%;
}

/* experience section
==================================================*/

#experience {
  padding: 100px 0;
  background: #f3f3f3;
}

.experience-row {
  padding: 20px 0;
  border-bottom: 1px solid #ddd;
}

.experience-row h3 {
  font-family: 'Lato', sans-serif;
  text-transform: capitalize;
  letter-spacing: 2px;
  color: #425bb5;
  font-size: 16px;
  font-weight: bold;
}

.experience-row p {
  color: #888;
  margin-top: 20px;
  font-size: 15px;
}

.experience-row .date {
  color: #000;
  font-size: 17px;
}

.timeline {
  padding-left: 0;
  list-style: none;
  position: relative;
}

.timeline:before {
  background-color: black;
  content: '';
  margin-left: -1px;
  position: absolute;
  top: 0;
  left: 2em;
  width: 2px;
  height: 100%;
}

.timeline-event {
  position: relative;
}

.timeline-event:hover .timeline-event-icon {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  background-color: #425bb5;
}

.timeline-event:hover .timeline-event-thumbnail {
  box-shadow: inset 40em 0 0 0 #425bb5;
}

.timeline-event-copy {
  padding: 2em;
  position: relative;
  top: -1.875em;
  left: 4em;
  width: 100%;
}

.timeline-event-copy h3 {
  font-size: 1.75em;
}

.timeline-event-copy h4 {
  font-size: 1.2em;
  margin-bottom: 1.2em;
}

.timeline-event-copy strong {
  font-weight: 700;
}

.timeline-event-copy p:not(.timeline-event-thumbnail) {
  padding-bottom: 1.2em;
}

.timeline-event-icon {
  -webkit-transition: -webkit-transform 0.2s ease-in;
  transition: -webkit-transform 0.2s ease-in;
  transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background-color: #425bb5;
  outline: 10px solid #f3f3f3;
  display: block;
  margin: 0.5em 0.5em 0.5em -0.5em;
  position: absolute;
  top: 0;
  left: 2em;
  width: 1em;
  height: 1em;
}

.timeline-event-thumbnail {
  -moz-transition: box-shadow 0.5s ease-in 0.1s;
  -o-transition: box-shadow 0.5s ease-in 0.1s;
  -webkit-transition: box-shadow 0.5s ease-in;
  -webkit-transition-delay: 0.1s;
  -webkit-transition: box-shadow 0.5s ease-in 0.1s;
  transition: box-shadow 0.5s ease-in 0.1s;
  color: white;
  font-size: 12px;
  font-weight: bold;
  background-color: black;
  box-shadow: inset 0 0 0 0em #425bb5;
  display: inline-block;
  margin-bottom: 1.2em;
  padding: 0.25em 1em 0.2em 1em;
}

/* projects section
==================================================*/

#projects {
  padding: 100px 0;
}

.portfolio .categories-grid span {
  font-size: 30px;
  margin-bottom: 30px;
  display: inline-block;
}

.portfolio .categories-grid .categories ul li {
  list-style: none;
  margin: 20px 0;
}

.portfolio .categories-grid .categories ul li a {
  display: inline-block;
  color: #60606e;
  padding: 0 10px;
  margin: 0 14px;
  -webkit-transition: all 0.2s ease-in-out 0.2s;
  transition: all 0.2s ease-in-out 0.2s;
}

.portfolio .categories-grid .categories ul li a:hover,
.portfolio .categories-grid .categories ul li a:focus {
  text-decoration: none;
}

.portfolio .categories-grid .categories ul li a.active {
  background-color: #425bb5;
  padding: 0px 20px;
  color: white;
  text-decoration: none;
}

.portfolio_filter {
  padding-left: 0;
}

.portfolio_item {
  position: relative;
  overflow: hidden;
  display: block;
  margin-bottom: 30px;
}

.portfolio_item .portfolio_item_hover {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-color: #425bb5;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  opacity: 0;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  color: #333;
}

.portfolio_item .portfolio_item_hover .item_info {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding: 10px;
  width: 100%;
  font-weight: bold;
}

.portfolio_item .portfolio_item_hover .item_info span {
  display: block;
  color: #fff;
  font-size: 18px;
  -webkit-transform: translateY(-100px);
  transform: translateY(-100px);
  -webkit-transition: all 0.3s ease-in-out 0.3s;
  transition: all 0.3s ease-in-out 0.3s;
  opacity: 0;
}

.portfolio_item .portfolio_item_hover .item_info em {
  font-style: normal;
  display: inline-block;
  background-color: #425bb5;
  padding: 5px 20px;
  color: #fff;
  margin-top: 10px;
  -webkit-transform: translateY(100px);
  transform: translateY(100px);
  -webkit-transition: all 0.3s ease-in-out 0.3s;
  transition: all 0.3s ease-in-out 0.3s;
  opacity: 0;
  font-size: 10px;
  letter-spacing: 2px;
}

.portfolio_item:hover .portfolio_item_hover {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.portfolio_item:hover .item_info em,
.portfolio_item:hover .item_info span {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.portfolio .categories-grid .categories ul li {
  float: left;
}

.portfolio .categories-grid .categories ul li a {
  padding: 0 10px;
  -webkit-transition: all 0.2s ease-in-out 0.2s;
  transition: all 0.2s ease-in-out 0.2s;
}

.portfolio_filter {
  padding-left: 0;
  display: inline-block;
  margin: 0 auto;
  text-align: center;
}

.portfolio_item img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.portfolio-padding {
  padding: 50px 0;
}

.portfolio-inner {
  padding-bottom: 0 !important;
  padding-top: 55px;
}

.modal-content {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0);
  border: none;
}

.popup-modal {
  background: #fff;
}

.close-popup-modal {
  float: right;
  margin: 20px;
  font-size: 22px;
  opacity: 0;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transition-delay: 0.6s;
  transition-delay: 0.6s;
  position: fixed;
  z-index: 100000;
  right: 14px;
}

.animatedModal-on .close-popup-modal {
  opacity: 1;
}

.modal-content h2 {
  font-family: 'Playfair Display', serif;
  font-size: 40px;
  line-height: 50px;
}

/* contact
==================================================*/

#contact {
  padding: 100px 0;
  background: #f3f3f3;
}

#contact-form input,
#contact-form textarea {
  height: 50px;
  margin-bottom: 10px;
  border-radius: 0;
}

#contact-form label {
  display: block;
  margin-bottom: 0.2em;
  font-size: 13px;
  line-height: 13px;
  font-size: 1.3rem;
  line-height: 1.3rem;
  color: #000;
}

#contact-form label.error {
  margin-top: 10px;
  margin-bottom: 1em;
  font-size: 12px;
  line-height: 12px;
  font-size: 1.2rem;
  line-height: 1.2rem;
  color: #d33e43;
  margin-left: 10px;
}

#contact-form input[type='text'].error,
#contact-form input[type='password'].error,
#contact-form input[type='date'].error,
#contact-form input[type='datetime'].error,
#contact-form input[type='datetime-local'].error,
#contact-form input[type='month'].error,
#contact-form input[type='week'].error,
#contact-form input[type='email'].error,
#contact-form input[type='number'].error,
#contact-form input[type='search'].error,
#contact-form input[type='tel'].error,
#contact-form input[type='time'].error,
#contact-form input[type='url'].error,
#contact-form textarea.error {
  border-color: #d33e43;
}

textarea {
  resize: none;
}

fieldset {
  border: 0px;
  margin: 0;
  padding: 0;
}

.required {
  color: #e9266d;
}

#success,
#error {
  display: none;
}

#success p,
#error p {
  display: block;
}

#success p {
  color: #425bb5;
  font-size: 15px;
  text-align: center;
  padding: 10px;
  border: 2px solid #425bb5;
  margin-top: 10px;
}

#error p {
  color: #d33e43;
  font-size: 15px;
  text-align: center;
  padding: 10px;
  border: 2px solid #d33e43;
  margin-top: 10px;
}

#submit {
  background-color: #425bb5;
  color: #fff;
  padding: 10px 40px;
  border-radius: 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  width: 100%;
}

.social {
  padding-left: 0;
  list-style: none;
  margin-top: 20px;
}

.social li {
  float: left;
}

.social li a {
  color: #333;
  font-size: 35px;
  margin-right: 15px;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}

.social li a:hover {
  color: #425bb5;
}

/* Responsive media queries
==================================================*/

@media (max-width: 991px) {
  .portfolio .categories-grid .categories ul li a {
    padding: 0 2px;
  }
}

@media (max-width: 768px) {
  .timeline-event-copy {
    width: 90%;
    left: 2em;
  }
}

@media (max-width: 500px) {
  h1 {
    font-family: 'Playfair Display', serif;
    font-size: 50px;
    line-height: 50px;
  }
  #home,
  .disply-table {
    height: 600px;
  }
  .about-border {
    width: 265px;
  }
  .portfolio .categories-grid .categories ul li {
    list-style: none;
    margin: 10px 0;
  }
}

@media (max-width: 420px) {
  nav li {
    margin: 6px;
  }
  nav li a {
    font-size: 10px;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #fbfbfb;
}

.wrapper {
  width: 500px;
  margin: 10px auto;
  font-size: 14px;
  background-color: #fff;
  border: 1px solid #f1f1f1;
  padding: 20px;
}

/* 评论框样式 */
.comment-input {
  background-color: #fff;
  border: 1px solid #f1f1f1;
  padding: 20px;
  margin-bottom: 10px;
}

.comment-field {
  margin-bottom: 15px;
  display: flex;
}

.comment-field .comment-field-name {
  display: flex;
  flex-basis: 100px;
  font-size: 14px;
}

.comment-field .comment-field-input {
  display: flex;
  flex: 1 1;
}

.comment-field-input input,
.comment-field-input textarea {
  border: 1px solid #e6e6e6;
  border-radius: 3px;
  padding: 5px;
  outline: none;
  font-size: 14px;
  resize: none;
  flex: 1 1;
}

.comment-field-input textarea {
  height: 100px;
}

.comment-field-button {
  display: flex;
  justify-content: flex-end;
}

.comment-field-button button {
  padding: 5px 10px;
  width: 80px;
  border: none;
  border-radius: 3px;
  background-color: #00a3cf;
  color: #fff;
  outline: none;
  cursor: pointer;
}

.comment-field-button button:active {
  background: #13c1f1;
}

/* 评论列表样式 */
.comment-list {
  background-color: #fff;
  border: 1px solid #f1f1f1;
  padding: 20px;
}

/* 评论组件样式 */
.comment {
  position: relative;
  display: flex;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 10px;
  padding-bottom: 10px;
  min-height: 50px;
}

.comment .comment-user {
  flex-shrink: 0;
}

.comment-username {
  color: #00a3cf;
  font-style: italic;
}

.comment-createdtime {
  padding-right: 5px;
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 5px;
  font-size: 12px;
}

.comment:hover .comment-delete {
  //color: #00a3cf;
  color: red;
}

.comment-delete {
  position: absolute;
  right: 0;
  top: 0;
  
  //color: transparent;
  //color: pink;
  //opacity:0.3;
  color:rgba(120,120,120, 0.5);
  font-size: 12px;
  cursor: pointer;
}

.comment p {
  margin: 0;
  /*text-indent: 2em;*/
}

code {
  border: 1px solid #ccc;
  background: #f9f9f9;
  padding: 0px 2px;
}
